import { SolanaSignInInput } from "@solana/wallet-standard-features";
import axios from 'axios';
import Cookies from 'js-cookie';
export const API_END_POINT = process.env.REACT_APP_BACKEND_END_POINT;

export const createSignInData = async (): Promise<SolanaSignInInput> => {
    const signInData = await axios.get(API_END_POINT + '/users/create-sign');
    return signInData.data;
};
  
export const createSignInErrorData = async (): Promise<SolanaSignInInput> => {  
    const now: Date = new Date();
    // Convert the Date object to a string
    const currentDateTime = now.toISOString();
    const signInData: SolanaSignInInput = {
        domain: "phishing.com",
        statement: "Sign-in to connect!",
        uri: "https://www.phishing.com",
        version: "1",
        nonce: "oBbLoEldZs",
        chainId: "solana:mainnet",
        issuedAt: currentDateTime,
        resources: ["https://example.com", "https://phantom.app/"]
    };
    return signInData;
};

export const isValidToken = (walletAddress) => {  
    const jwtToken = Cookies.get('token');
    if(jwtToken !== undefined){
        const jwtPayload = JSON.parse(window.atob(jwtToken.split('.')[1]));
        if(jwtPayload.name == walletAddress){
            return true;
        }else{
            return false;
        }
    }
    return false;
};