import React, { useMemo, useCallback } from "react";
import { Toaster } from "react-hot-toast";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Cookies from 'js-cookie';
import axios from 'axios';


import {
  PhantomWalletAdapter,
  Coin98WalletAdapter,
  SolflareWalletAdapter,
  BitgetWalletAdapter,
  BitpieWalletAdapter,
  CloverWalletAdapter,
  CoinbaseWalletAdapter,
  CoinhubWalletAdapter,
  HuobiWalletAdapter,
  LedgerWalletAdapter,
} from "@solana/wallet-adapter-wallets";

import {
  ConnectionProvider,
  WalletProvider,
} from "@solana/wallet-adapter-react";

import type { Adapter } from '@solana/wallet-adapter-base';
import { WalletModalProvider } from "@solana/wallet-adapter-react-ui";

import type { SolanaSignInInput } from '@solana/wallet-standard-features';

import { createSignInData } from "./web3/auth";

import TemplateLayout from "./components/Layout";
import Game from "./page/Game";
import RoundDetail from "./page/RoundDetail";
import History from "./page/History";
import Referral from "./page/Referral";
import Reward from "./page/Reward";
import Home from "./page/Home";
import Test from "./page/Test";


import logo from "./assets/img/logo.svg";


import "./App.css";

require("@solana/wallet-adapter-react-ui/styles.css");

const API_END_POINT = process.env.REACT_APP_BACKEND_END_POINT;

function App() {
  const rpc = process.env.REACT_APP_RPC_END_POINT
    ? process.env.REACT_APP_RPC_END_POINT
    : "";
  let network = rpc;
  const endpoint = useMemo(() => rpc, [network]);
  const wallets = useMemo(
    () => [
      new PhantomWalletAdapter(),
      new SolflareWalletAdapter(),
      new LedgerWalletAdapter()
    ],
    [network]
  );

  const autoSignIn = useCallback(async (adapter: Adapter) => {
    if (!('signIn' in adapter)) return true;
    if(typeof Cookies.get('token') !== "undefined"){
      // check token
      const jwtToken = Cookies.get('token');
      const jwtPayload = JSON.parse(window.atob(jwtToken.split('.')[1]))
      const isExpired = Date.now() >= jwtPayload.exp * 1000;

      if(isExpired){
        // clear token
        Cookies.remove('token');
        return false;
      }
      return true;
    }
    const input: SolanaSignInInput = await createSignInData();
    const output = await adapter.signIn(input);
    const address = output.account.address;
    let refFrom = "";
    if(typeof Cookies.get('ref_from') !== "undefined"){
      refFrom = Cookies.get('ref_from');
    }
    const verifyResponse = await axios.post(API_END_POINT + '/users/verify', {input, output, address, refFrom});
    Cookies.set('token', verifyResponse.data.token);
    return false;
  }, []);

  return (
    // <ConnectionProvider endpoint={endpoint}>
    //   <WalletProvider wallets={wallets} autoConnect>
    //     <WalletModalProvider>
    //       <Router>
    //         <TemplateLayout>
    //           <Routes>
    //             <Route path="/" element={<Home />} />
    //             <Route path="/home" element={<Home />} />
    //             <Route path="/game" element={<Game />} />
    //             <Route path="/history/:roundIndex" element={<RoundDetail />} />
    //             <Route path="/reward" element={<Reward />} />
    //             <Route path="/history" element={<History />} />
    //             <Route path="/referral" element={<Referral />} />


    //             <Route path="/test" element={<Test />} />
    //           </Routes>
    //         </TemplateLayout>
    //       </Router>

    //       <Toaster
    //         position="top-center"
    //         toastOptions={{
    //           duration: 2500,
    //         }}
    //       />
    //     </WalletModalProvider>
    //   </WalletProvider>
    // </ConnectionProvider>
    <div className="flex h-[100vh] justify-center items-center flex-col bg-[#1c0d34]">
      <img className="max-w-[400px]" src={logo} />
      <p className="text-[18px] mb-10">Play the way you want</p>
    </div>
  );
}

export default App;
